
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Picker from 'vux/src/components/picker/index.vue'

import allData from '../../assets/js/localData'
export default {
components: {
XButton,
Flexbox,
FlexboxItem,
Grid,
GridItem,
Checker,
CheckerItem,
Picker
},
data () {
return {
filter: {
sex: '0',
year: [],
home: [],
work: []
},
status: {
sex: false,
year: false,
home: false,
work: false
},
allLocalData: allData
}
},
watch: {
'filter.sex' (val) {
console.log(val)
},
'filter.year' (val) {
console.log(val)
}
},
methods: {
resetfalse (key) {
switch (key) {
case 0:
this.status.sex = false
this.status.year = false
this.status.home = false
this.status.work = false
break
case 1:
this.status.year = false
this.status.home = false
this.status.work = false
break
case 2:
this.status.sex = false
this.status.home = false
this.status.work = false
break
case 3:
this.status.sex = false
this.status.year = false
this.status.work = false
break
case 4:
this.status.sex = false
this.status.year = false
this.status.home = false
break
}
},
sexClick () {
this.resetfalse(1)
this.status.sex = !this.status.sex
},
yearClick () {
this.resetfalse(2)
this.status.year = !this.status.year
},
homeClick () {
this.resetfalse(3)
this.status.home = !this.status.home
},
workClick () {
this.resetfalse(4)
this.status.work = !this.status.work
},
homeChange (val) {
console.log(val)
},
lookResume () {
this.$router.push({path: '/resumeDetail'})
}
}
}



